$Cloudfront: "https://static.fullstackhrivnak.com/mines";
.header-nav {
  text-rendering: geometricPrecision;
  margin-left: auto;
  padding-bottom: 1%;
  .header-nav-ul {
    display: flex;
    background-color: teal;
    .header-nav-ul-li {
      display: flex;
      border: 1px solid #fffff0;
      flex: 1;
      justify-content: center;

      .logged-in-user-title, a {
        color: ivory;
      }



      a {
        display: flex;
        justify-content: center;
        text-decoration: none;
        padding: 0.5em 4%;
        width: 100%;

        &:hover {
          cursor: pointer;
        }
      }
      &:hover {
        background-color: darkslategray;
      }
    }

    .selected-header {
      background-color: darkslategrey
    }
  }
}